&__share {
  $root: &;

  &.is-global {
    #{$root}--unit {
      display: flex;
      flex-direction: column;
      align-items: center; }

    #{$root}--term {
      color: $font-white;
      font: {
        size: $font-xx-small;
        weight: bold;
        family: sans-serif; }
      writing-mode: vertical-rl;
      text-shadow: 0 0 4px rgba($bg-black, 0.2);
 }      // transform: rotate(90deg)

    #{$root}--description {
      margin-top: 20px; } } }
