&__calendar {
  $root: &;
  width: 100%;
  height: 100%;
  position: relative;

  &.is-saturday {
    #{$root}--item {
      &.is-date {
        color: $font-light-blue; } } }

  &.is-holiday,
  &.is-sunday {
    #{$root}--item {
      &.is-date {
        color: $font-light-red; } } }

  &.is-today {
    #{$root}--item {
      &.is-date,
      &.is-event {
        color: $font-yellow; } } }

  & &--item {
    width: 100%;
    display: block;
    text-align: center;

    &.is-date {
      position: absolute;
      top: 0.5em;
      left: 0;
      line-height: 1;
      font: {
        size: $font-x-large;
        weight: bold;
        family: Arial, sans-serif; } }

    &.is-event {
      font-size: $font-xx-small;
      padding-top: 4em; } }

  @include breakpoint($max-media: sp) {
    #{$root}--item {
      &.is-date {
        font-size: $font-normal; } } } }

