&__banner {
  $root: &;

  &.is-home {
    margin: #{$global-margin-pc}px 0;

    &:first-child {
      margin-top: 0; }

    &:last-child {
      margin-bottom: 0; }

    #{$root}--item {
      width: 100%;
      margin-top: 20px;
      border: 8px solid $border-gray;

      &:first-child {
        margin-top: 0; } }

    @include breakpoint {
      margin: #{$global-margin-sp}px 0; }

    @include breakpoint($max-media: sp) {
      #{$root}--item {
        border: 4px solid $border-gray; } } } }
